@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&family=Maven+Pro:wght@700&display=swap');

@layer base {
  html, body, #root {
    @apply h-full;
  }

  .font-lato {
    font-family: 'Lato', sans-serif;
  }
  .font-maven {
    font-family: 'Maven Pro', sans-serif;
  }
}

@layer components {
  .App {
    @apply h-full flex items-center justify-center w-2/3 md:w-full mx-auto;
  }

  .counter {
    @apply grid grid-cols-2 sm:grid-cols-3 md:grid-cols-6 gap-8 font-lato transition-colors text-slate-900 dark:text-slate-100;
  }

  .counter > div {
    @apply transition-colors p-6 rounded-lg bg-white/20 dark:bg-black/30 text-center;
  }
  .counter > div > p:first-child {
    @apply text-2xl font-black;
  }

  .counter > div.social-links {
    @apply col-span-2 sm:col-span-3 md:col-span-6 grid grid-cols-4 sm:grid-cols-2 md:grid-cols-4 gap-6 items-center justify-center md:gap-x-6 text-slate-900 dark:text-slate-100;
  }

  div.social-links > a {
    @apply inline-flex items-center sm:w-max mx-auto w-10 h-10 sm:gap-x-2 bg-black/10 dark:bg-white/5 rounded-xl sm:px-4 sm:py-2 p-2 transition-colors hover:bg-black/20 dark:hover:bg-white/10;
  }
  div.social-links > a > p {
    @apply sm:block hidden;
  }

  .theme-holder {
    @apply absolute md:left-1/2 md:-translate-x-1/2 right-4 md:right-auto bottom-4 p-2 transition-colors bg-white/30 dark:bg-black/30 text-slate-900 dark:text-slate-100 inline-flex gap-x-2 rounded-xl;
  }

  .theme-holder > button {
    @apply inline-flex p-2 rounded-lg transition-all;
  }
}